
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
name: "myAddress",
components: {
ViewBox,
XHeader,
Card,
Loading
},
data() {
return {
addressList: [],
isSelect: false,
selectAddressId: ''
};
},
created () {
if (this.$route.name === "selectAddress") {
this.isSelect = true;
this.selectAddressId = this.$route.params.addressId;
} else {
this.isSelect = false;
}
this.queryUserAddress();
},
methods: {
toEditAddress(addressItem) {
this.$router.push({
name: 'editAddress',
params: { addressInfo: addressItem }
})
},
queryUserAddress() {
this.$vux.loading.show();
this.$api
.queryUserAddress({})
.then(data => {
if (data.code === 200) {
this.addressList = data.data.address;
}
this.$vux.loading.hide();
});
},
toGetGoods(item) {
if(!this.isSelect) return;
this.$router.push({
name: 'getGoods',
params: {
addressId: item.id,
addressName: item.province + item.city + item.area + item.detail
}
})
}
}
};



